<template>
	<view>
		<view v-show="!showCoupon">
			<!-- 地址 -->
			<navigator
				:url="`/pages/address/address?source=1&orderData=show&facdureData=` + JSON.stringify(facdureData)"
				class="address-section">

				<view class="order-content">
					<text class="yticon icon-shouhuodizhi"></text>
					<view class="cen" v-if="addressData.name">
						<view class="top">
							<text class="name">{{addressData.name}}</text>
							<text class="mobile">{{addressData.mobile}}</text>
						</view>
						<text class="address">{{addressData.address}} {{addressData.roomNum}}</text>
					</view>
					<view class="cen" v-else>您还没有添加收货地址</view>
					<text class="yticon icon-you"></text>
				</view>

				<image class="a-bg"
					src="data:image/png;base64,">
				</image>
			</navigator>

			<view class="goods-section">
				<view class="g-header b-b">
					<text class="name">商品信息</text>
				</view>
				<!-- 商品列表 -->
				<block v-for="(item,index) in goodsList" :key="index">
					<view style="margin-bottom: 20upx">
						<view class="g-item">
							<image :src="item.imgPath"></image>
							<view class="right">
								<text class="title clamp">{{item.productName}}</text>
								<view class="price-box">
									<text class="price">{{item.scoreNumber}}</text>
									<text class="number">x {{item.productNum}}</text>
								</view>
							</view>
						</view>
					</view>
				</block>
			</view>


			<!-- 金额明细 -->
			<view class="yt-list">
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">商品合计</text>
					<text class="cell-tip red">{{goodsPrice}}积分</text>
				</view>

				<view class="yt-list-cell desc-cell">
					<text class="cell-tit clamp">备注信息</text>
					<input class="desc" type="text" v-model="remarks" placeholder="选填,给商家留言"
						placeholder-class="placeholder" />
				</view>
			</view>

			<!-- 底部 -->
			<view class="footer">
				<view class="price-content">
					<text>应付 </text>
					<text class="price">{{total}}</text>
				</view>
				<text class="submit" @click="submit">提交订单</text>
			</view>


		</view>

	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex';
	import {
		fn
	} from '@/util/common.js';
	export default {
		data() {
			return {
				showCoupon: false,

				orderData: "",
				subFlag: true,
				total: 0, //总价格
				goodsPrice: 0, //商品价格
				goodsList: [],
				maskState: 0, //优惠券面板显示状态
				remarks: '', //备注
				payType: 1, //1微信 2支付宝


				addressData: {
					code: ''
				},
				facdureData: {
					code: '',
					payable: '不开发票'
				},
				couponData: {
					useCoupon: 0,
					couponTypeCode: "",
					couponCode: '',
					minPoint: 0,
					couponAmount: 0,
					payable: '不使用优惠券'
				}
			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo'])
		},
		onLoad(option) {
			let returnUrl = getCurrentPages()[0].route;
			console.log('当前路由：', returnUrl);
			if (!this.hasLogin) {
				uni.reLaunch({
					url: '/pages/wxAuth/wxAuth?returnUrl=' + returnUrl
				})
			}
			let tmpOrderData = uni.getStorageSync('scoreOrderData') || '';
			console.log('临时数据：', tmpOrderData)
			this.orderData = option.data;
			//商品数据
			this.goodsList = tmpOrderData;

			if (option.addressData) {
				this.addressData = JSON.parse(option.addressData)
			} else {
				this.getAddrData();
			}

			this.calcTotal(); //计算总价
		},
		methods: {


			//获取地址
			async getAddrData() {
				//uni.showNavigationBarLoading();
				var params = {
					pageNo: 1,
					pageSize: 1
				}
				let res = await this.$axios(this.$baseUrl.getContactsList, JSON.stringify(params));
				if (res.data.code == 200) {
					if (res.data.data.length) {
						this.addressData = res.data.data[0];
					}

				}
			},
			//计算总价
			calcTotal() {
				let list = this.goodsList;
				let total = 0;
				list.forEach(item => {
					total += item.scoreNumber * item.productNum;
				})

				this.total = Number(total.toFixed(2));
				this.goodsPrice = Number(total.toFixed(2));


			},

			async submit() {
				//this.$api.msg('收货地址不能为空');
				if (!this.addressData.code) {
					this.$api.msg('收货地址不能为空');
					return;
				}

				if (this.subFlag) {
					var params = {
						"goodsCode": this.goodsList[0].productCode,
						"num": this.goodsList[0].productNum,
						"remarks": this.remarks,
						"shopContactsCode": this.addressData.code
					}
					console.log("参数详情：1111", params)
					let res = await this.$axios(this.$baseUrl.scoreGoodsCreate, params);
					console.log(res.data,'11111')
					if (res.data.code == 200) {
						this.subFlag = true;
						this.$api.msg(res.data.message);
						
						uni.removeStorage({
							key: 'scoreOrderData'
						})
						setTimeout(()=>{
							uni.redirectTo({
								url: '/pages/accumulate/order'
							})
						},1500)
					}
				}
			},
			stopPrevent() {}
		}
	}
</script>

